var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Nàng công chúa chăn ngỗng - Truyện cổ Grimm")])]),_c('h3',[_c('span',{},[_c('em',[_vm._v("Cô công chúa xinh đẹp chia tay mẹ đến nước xa lạ để kết hôn với hoàng tử. Trên đường đi nàng bị thị nữ hãm hại, khi đến nơi nàng phải đi chăn ngỗng. Nhưng cuối cùng nàng công chúa đã được kết hôn cùng hoàng tử, còn thị nữ độc ác kia bị xử phạt thích đáng.")])])]),_vm._v(" Truyện cổ tích Nàng công chúa chăn ngỗng"),_c('br'),_c('br'),_vm._v(" Ngày xưa có một bà hoàng hậu tuổi đã cao. Đức vua chết đã lâu. Bà có một cô con gái rất xinh đẹp. Khi lớn lên, nàng được hứa hôn với một chàng Hoàng tử con vua một nước xa xôi."),_c('br'),_c('br'),_vm._v(" Đến ngày tổ chức lễ cưới, nàng công chúa sửa soạn đi đến nước xa lạ. Mẹ nàng gói ghém cho nàng những vật quí giá: đồ trang sức, vàng, bạc, cốc, châu ngọc , tóm lại là tất cả những gì xứng đáng làm của hồi môn cho một công chúa, vì mẹ nàng rất mực thương nàng. Mẹ nàng gửi gắm nàng cho một thị nữ có nhiệm vụ dẫn nàng đi đến chỗ ở người chồng chưa cưới. Mỗi người cưỡi một con ngựa. Ngựa công chúa cưỡi tên là Phalađa, biết nói."),_c('br'),_c('br'),_vm._v(" Đến lúc chia tay, bà hoàng vào trong phòng ngủ, lấy một con dao nhỏ trích ngón tay, để chảy ba giọt máu. Bà cho máu thấm xuống một cái khăn trắng nhỏ, đưa cho cô gái và dặn: “Con thân yêu, con hãy giữ gìn cẩn thận cái khăn này, nó sẽ có ích cho con trên đường đi”."),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Hai mẹ con buồn bã từ biệt nhau. Công chúa áp cái khăn lên ngực, nhảy lên yên ngựa để đi đến nơi ở của người yêu. Sau khi đã đi một tiếng, cô cảm thấy khát khô họng, bèn bảo thị nữ:"),_c('br'),_c('br'),_vm._v(" – “Em hãy xuống ngựa, lấy cốc của ta múc nước suối kia và mang lại đây cho ta, ta khát nước lắm”."),_c('br'),_c('br'),_vm._v(" – “Nếu cô khát, thị nữ trả lời, thì hãy tự nhảy xuống, rồi vươn người ra trên mặt nước mà uống. Tôi không phải là đầy tớ của cô”."),_c('br'),_c('br'),_vm._v(" Công chúa khát lắm, bèn xuống ngựa, cúi xuống dòng nước suối để uống nước. Nàng không dám uống nước bằng cốc vàng. “Trời ơi!” nàng kêu to. Ba giọt máu bảo cô: “Nếu mẹ cô biết sự tình thế này, thì hẳn tim bà sẽ tan nát trong ngực”."),_c('br'),_c('br'),_vm._v(" Nhưng công chúa là người can đảm. Nàng không nói gì và lại nhảy lên ngựa. Ngựa phi được vài dặm. Trời thì nóng nực, chẳng mấy chốc nàng lại khát nước."),_c('br'),_c('br'),_vm._v(" Tới một con sông, nàng bảo thị nữ: “Em hãy xuống ngựa và cho ta uống nước bằng cái cốc vàng”. Cô đã quên đứt những lời độc ác của thị nữ. Nhưng thị nữ lại trả lời ngạo mạn hơn: “Nếu cô khát thì tự đi uống nước một mình, tôi không phải là đầy tớ của cô”."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Công chúa khát quá, nhảy xuống ngựa, cúi xuống dòng nước chảy xiết, khóc và kêu lên: “Trời ơi!” Ba giọt máu liền đáp lại: “Nếu mẹ cô biết sự tình thế này, thì hẳn tim bà tan nát trong ngực”. Trong khi cô cúi xuống để uống thì cái khăn có thấm ba giọt máu, tuột khỏi ngực cô và trôi theo dòng nước mà cô không hay biết, vì lúc đó cô rất sợ hãi."),_c('br'),_c('br'),_vm._v(" Thị nữ thì lại trông thấy hết và nó rất vui mừng từ giờ trở đi công chúa sẽ bị nó trả thù. Từ lúc đánh mất ba giọt máu, công chúa trở nên yếu đuối, không đủ sức tự vệ nữa. Khi nàng định trèo lên con ngựa Phalađa thì thị nữ bảo: “Tôi sẽ cưỡi con Phalađa, còn cô, cô hãy cưỡi con ngựa tồi của tôi”."),_c('br'),_c('br'),_vm._v(" Công chúa đành làm như vậy. Tiếp đó thị nữ ra lệnh, lời lẽ gay gắt, bắt nàng phải cởi quần áo hoàng cung ra và mặc quần áo của nó vào. Cô lại phải thề với trời đất là khi đến cung điện sẽ không nói lộ ra. Nếu cô không chịu thề thì nó sẽ giết cô tại chỗ. Nhưng con Phalađa đã quan sát tất cả và ghi nhớ tất cả. Thị nữ cưỡi con Phalađa, còn công chúa thì cưỡi con ngựa tồi. Nó lại tiếp tục đi, cuối cùng đến lâu đài nhà vua. Ở đấy, mọi người rất vui mừng khi họ tới, và Hoàng tử vội chạy tới tận nơi đón họ, đỡ thị nữ xuống ngựa, vì tưởng rằng đó là vợ chưa cưới của mình. Thị nữ đi lên bậc thềm lâu đài, còn nàng công chúa thì phải đứng lại ngoài sân."),_c('br'),_c('br'),_vm._v(" Vua cha nhìn ra, qua cửa sổ thấy nàng duyên dáng và tuyệt đẹp. Người vào trong cung hỏi cô gái được coi là vợ chưa cưới của Hoàng tử xem cô gái đứng ngoài sân là ai. “Tâu vua cha, con đã gặp cô gái đó trên đường đi và con đưa cô ta đi cùng để đỡ lẻ loi một mình. Xin vua cha cho cô ta làm việc để cô ta khỏi phải vô công rồi nghề”. Nhưng vua cha không có việc gì giao cho cô làm cả."),_c('br'),_c('br'),_vm._v(" Người bảo: “Ở ngoài kia, ta có một anh chàng chăn ngỗng, hãy để cô ta giúp việc vậy”. Chàng trai tên là Cuốc. Vợ chưa cưới của Hoàng tử phải giúp anh chăn ngỗng."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Ít lâu sau, vợ chưa cưới giả tâu với hoàng tử: “Chàng thân yêu ơi, em muốn một điều, chàng hãy làm vui lòng em”. Hoàng tử nói: “Được thôi!” “Chàng hãy cho gọi người thợ lột da đến đập chết con ngựa em đang cưỡi đến đây, vì trong khi đi đường nó làm em bực tức”."),_c('br'),_c('br'),_vm._v(" Thật ra thì nó sợ con ngựa kể lại cách nó đã đối xử với công chúa. Đến lúc con ngựa trung thành Phalađa phải chết thì công chúa được tin. Nàng hứa với người thợ lột da là sẽ bí mật biếu anh một đồng tiền bằng bạc nếu anh giúp nàng một việc nhỏ. Trong đô thị có một cái cổng to rất tối, hàng ngày, sớm tối nàng phải dẫn đàn ngỗng đi qua. Nàng xin người thợ lột da hãy đóng đanh treo đầu con Phalađa vào cái cổng ấy để nàng có thể luôn luôn trông thấy nó."),_c('br'),_c('br'),_vm._v(" Người thợ lột da hứa sẽ làm và bác đóng chặt đanh treo đầu ngựa vào dưới cái cổng tối om. Sáng sớm, khi cùng Cuốc đi qua cổng, cô bảo cái đầu: “Ôi, Phalađa, mày bị treo ở đây ư!” Cái đầu trả lời: “Ôi! Nàng công chúa của tôi, nàng qua đây ư! Nếu mẹ nàng biết nông nỗi này tim mẹ sẽ vỡ tan tành”. Lặng lẽ, cô đi khỏi đô thị, dẫn đàn ngỗng ra cánh đồng."),_c('br'),_c('br'),_vm._v(" Đến đồng cỏ, cô ngồi xuống và rũ tóc ra. Tóc cô óng ánh như vàng nguyên chất và Cuốc rất thích nhìn mớ tóc ấy lóe sáng. Anh muốn nhổ vài sợi tóc. Công chúa bèn nói: “Ta khóc đây, ta khóc đây! Hỡi làn gió nhẹ. Hãy cuốn cái mũ của Cuốc đi! Anh ta sẽ chạy theo cái mũ cho đến khi nào tóc ta chải và tết xong”. Tức thì gió thổi mạnh cuốn đi cái mũ của Cuốc. Anh ta chạy theo ngay. Khi anh trở về thì công chúa đã chải đầu xong và anh không nhổ được sợi tóc nào. Anh rất bực và không nói năng gì với cô nữa."),_c('br'),_c('br'),_vm._v(" Họ lại tiếp tục chăn ngỗng đến chiều, rồi cùng về nhà. Sáng sớm hôm sau, khi lùa ngỗng qua cổng, cô gái nói: “Ôi, Phalađa, mày bị treo ở đây ư!”"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Cái đầu trả lời: “Ôi! Nàng công chúa của tôi, nàng qua đây ư! Nếu mẹ nàng biết nông nỗi này, tim mẹ sẽ vỡ tan tành”."),_c('br'),_c('br'),_vm._v(" Đi ra khỏi đô thị, cô lại ngồi trên đồng cỏ và lại rũ tóc ra chải. Cuốc muốn nắm lấy mớ tóc. Cô vội vàng nói: “Ta khóc đây, ta khóc đây! Hỡi làn gió nhẹ, hãy cuốn cái mũ của Cuốc đi! Anh ta sẽ chạy theo cái mũ cho đến khi nào tóc ta chải và tết xong”. Gió nổi lên, cuốn cái mũ đi. Cuốc phải chạy theo. Khi anh trở về thì công chúa đã chải đầu xong từ lâu và anh không nắm được mớ tóc ấy. Và rồi hai người lại cùng chăn ngỗng đến chiều."),_c('br'),_c('br'),_vm._v(" Nhưng chiều hôm ấy, về tới nhà, Cuốc đến gặp vua cha và tâu: “Kính thưa hoàng thượng, con không thể chăn ngỗng cùng cô gái này nữa” – “Tại sao vậy?”, vua hỏi. “Suốt ngày, cô ta làm con bực mình!” – Vua cha bảo anh kể lại sự việc đã xảy ra. Cuốc nói: “Buổi sáng, chúng con dẫn đàn ngỗng qua cái cổng tối om, ở đấy có một cái đầu ngựa treo trên tường. Cô ta nói với nó: “Ôi, Phalađa, mày bị treo ở đây ư!” Cái đầu trả lời: “Ôi! Nàng công chúa của tôi, nàng qua đây ư! Nếu mẹ nàng biết nông nỗi này tim mẹ sẽ vỡ tan tành.” Và Cuốc kể các sự việc đã xảy ra ở cánh đồng chăn ngỗng và tại sao anh ta lại phải chạy theo cái mũ. Vua cha dặn anh ta ngày hôm sau cứ đi chăn ngỗng như thường lệ."),_c('br'),_c('br'),_vm._v(" Sáng sớm ngài thân chinh đến dưới cái cổng tối om và nghe được những câu cô gái nói với cái đầu Phalađa. Ông theo ra cánh đồng và nấp vào một bụi cây. Chính mắt ngài trông thấy anh thanh niên và cô gái lùa ngỗng thế nào và sau một lúc, cô gái ngồi xuống gỡ mớ tóc vàng lóe sáng. Rồi cô lại nói: “Ta khóc đây, ta khóc đây! Hỡi làn gió nhẹ, hãy cuốn cái mũ của Cuốc đi! Anh ta sẽ chạy theo cái mũ cho đến khi nào tóc ta chải và tết xong”. Một cơn gió thổi mạnh, cuốn cái mũ đội đầu của Cuốc đi. Anh phải chạy theo rất xa. Cô gái chăn ngỗng chải tóc và cuốn thành từng búp. Vua cha nhìn thấy tất cả. Không ai nhận ra ngài khi ngài rời khỏi đó."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Chiều đến, cô gái về nhà, ngài cho gọi cô đến và hỏi tại sao cô lại làm như thế. “Tâu bệ hạ, con không thể nói được”, – cô trả lời. – “Con không thể kể nỗi khổ của con với bất cứ ai trên thế gian này, con đã thề như vậy để khỏi bị người ta giết”. Vua cha cô ép cô nói, nhưng ngài không biết được gì thêm bèn nói: “Nếu con không muốn nói với ta, thì con hãy kể nỗi khổ của con với cái bếp lò này”. Rồi ông bỏ đi."),_c('br'),_c('br'),_vm._v(" Cô đến ngồi gần cái bếp lò, than khóc, thổ lộ tâm can: “Ta ngồi đây, bị cả thế gian ruồng bỏ, dù ta là con vua. Một tên thị nữ độc ác đã áp chế ta, bắt ta đổi cho nó quần áo hoàng cung. Nó thay thế ta để làm vợ chưa cưới của người yêu ta, và ta bắt buộc phải làm công việc bình thường của người chăn ngỗng. Nếu mẹ ta biết nông nỗi này, tim bà sẽ tan nát”."),_c('br'),_c('br'),_vm._v(" Vua cha đứng ở phía tường bên kia gần ống thông hơi, ngài đã nghe thấy hết. Ngài trở về và gọi cô hãy rời cái lò đến gặp ngài. Người ta mang đến cho cô quần áo hoàng cung, cô mặc vào đẹp như là có phép lạ. Vua cha cho gọi con trai đến và bảo cho con biết về cô vợ chưa cưới giả mạo. Cô người yêu thật đứng trước chàng, đấy là cô gái chăn ngỗng."),_c('br'),_c('br'),_vm._v(" Hoàng tử thấy cô rất đẹp và phúc hậu nên lòng tràn ngập niềm vui. Một bữa tiệc được sửa soạn để mời tất cả các bạn bè thân thuộc. Hoàng tử và công chúa ngồi ở đầu bàn, trước mặt họ là con thị nữ. Nó bị choáng ngợp và không nhận ra cô chủ trang sức lộng lẫy. Khi họ đang ăn uống vui vẻ, vua cha ra một câu đố cho thị nữ. Nó phải trả lời là một người đàn bà lừa dối chủ thì sẽ bị xử tội như thế nào. Ngài kể các sự việc đã xảy ra và hỏi nó: “Như thế sẽ xứng đáng với hình phạt gì”. – “Nó xứng đáng phải đuổi đi khỏi đất nước” – “Kẻ ấy chính là mày, mày sẽ bị xử tội như mày nói”. Sau khi hình phạt được thi hành, Hoàng tử cưới nàng công chúa làm vợ và họ trị vì đất nước trong hòa bình và hạnh phúc."),_c('br'),_c('br'),_vm._v(" Nguồn: Truyện cổ tích Grimm ")])]),_c('div',{staticClass:"page",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
}
